
div.img-wrap {
    cursor: pointer;
    border-radius: 5px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.12), 0 2px 4px rgba(0,0,0,0.24);
    transition: box-shadow 0.3s cubic-bezier(.25,.8,.25,1);    
}
div.img-wrap:hover {
    z-index: 1000;
    transform: scale(1.125);
    box-shadow: 0 7px 14px rgba(0,0,0,0.25), 0 5px 5px rgba(0,0,0,0.22);
}
div.img-wrap .image {
    transition:opacity .5s ease;
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    backface-visibility: hidden;
    border-radius: 5px;
}

div.img-wrap div.text-wrap {
    transition:opacity .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

div.img-wrap:hover .image {
    opacity: 0.3;
}
  
div.img-wrap:hover div.text-wrap {
    opacity: 1;
}

div.img-wrap div.text-wrap div.text-content {
    font-size: 1rem;
    padding: 1rem;    
}

.ReactVirtualized__Masonry__innerScrollContainer {
    background: #eef6ff;
}




